import { useEffect, useState } from 'react';
import { Text,View,StyleSheet,Button,Linking,TextInput ,ScrollView} from 'react-native';
import * as SMS from 'expo-sms';
import FillScreen from './FillScreen';
import { SelectList } from 'react-native-dropdown-select-list'
import InputScrollView from 'react-native-input-scroll-view';



const FirstScreen =()=>{

    const[name,setName] = useState('');
    const[age,setAge] = useState('');
    const[problem,setProblem] = useState('');
    const[number,setNumber] = useState('');
    const [selected, setSelected] =useState('');
    
   
    const data = [
        {key:'1', value:'cough', disabled:false},
        {key:'2', value:'cold'},
        {key:'3', value:'fever'},
        {key:'4', value:'sore throat', disabled:false},
        {key:'5', value:'diarrhea'},
        {key:'6', value:'fatigue'},
        {key:'7', value:'nausea'},
    ]
    const [isAvailable,setIsAvailable]= useState(false);
    useEffect (async ()=>{
        const isSmsAvailable = await SMS.isAvailableAsync();
        setIsAvailable(isSmsAvailable);
    },[]);
    const actualMessage = name+'\n'+age+'\n'+problem + '\n'+ selected;
    const sendSms = async()=>{
        await SMS.sendSMSAsync(
            number,
            actualMessage        
         );
    };
    const sendWhatsapp = async()=>{
        //Linking.openURL('whatsapp://send?text=' + actualMessage + '&phone=' + number);
        Linking.openURL(
            'http://api.whatsapp.com/send?text=' + actualMessage + '&phone=' + number);
        

        
    }

    return(
        <ScrollView>
            <SelectList 
                 setSelected={(val) => setSelected(val)} 
                data={data} 
                save="value"/>
            <FillScreen
                number = {number}
                onNumberChange={setNumber}
                name = {name}
                onNameChange = {setName}
                age ={age}
                onAgeChange={setAge}
                problem={problem}
                onProblemChange = {setProblem}
                />
        {isAvailable ? <Button style={styles.buttonStyle} title= 'Send SMS' onPress={sendSms}/> :<Text>SMS not available</Text>}
        <Button style ={styles.buttonStyle} title = "send Whatsapp " onPress ={sendWhatsapp} />

        </ScrollView>
    )

};

const styles = StyleSheet.create({
    buttonStyle: {
        alignItems: 'center',
        justifyContent: 'center',
        paddingVertical: 12,
        paddingHorizontal: 32,
        borderRadius: 4,
        elevation: 3,
        backgroundColor: 'green'
     }
});

export default FirstScreen;

